import React from "react";
import "./Card.scss";

function Card({ card }) {
  const { img, title, description } = card;
  return (
    <div className="card">
      <div className="card__icon">{img}</div>
      <h5 className="card__title">{title}</h5>
      <p className="card__description">{description}</p>
    </div>
  );
}

export default Card;
