import React from "react";
import "./CardPersons.scss";

function CardPersons({ children, persons }) {
  if (!persons) {
    return <div className="loader">No Data</div>;
  }
  return (
    <div className="container">
      <div className="row">
        {persons.length !== 0 ? (
          persons.map(({ id, urls, user }) => {
            return (
              <div
                key={id}
                className="card-specialist col-12 col-md-6 col-lg-3"
              >
                <img
                  className="card-specialist__image"
                  src={urls.regular}
                  alt="person"
                />
                <h5 className="card-specialist__name">{user.name}</h5>
                <p className="card-specialist__profission">Dentist</p>
                <div className="d-flex justify-content-center">{children}</div>
              </div>
            );
          })
        ) : (
          <div className="my-5 col">
            <div className="loader">Loading...</div>;
          </div>
        )}
      </div>
    </div>
  );
}

export default CardPersons;
