import React from "react";
import "./AboutUs.scss";
import HalfSection from "../../components/HalfSection";
// import dataText from "./dataText";

function AboutUS({ img, revers, title, subtitle, paragraph, center }) {
  return (
    <section className="about">
      <HalfSection center={center} revers={revers} img={img}>
        <h3 className="about__title">
          {title}
          {subtitle && <span className="about__subtitle"><br />{subtitle}</span> }
          
        </h3>
        <p className="about__text">
         {paragraph}
        </p>
      </HalfSection>
    </section>
  );
}

export default AboutUS;
