import React, { useEffect, useState } from "react";
import AboutUS from "@layout/AboutUS";
import Specialist from "@layout/Specialist";
import InfoPage from "@layout/InfoPage";
import Clients from "@layout/Clients";
import Departments from "@layout/Departments";

import { getPhotos } from "../services/getService";

import { InfoPageData, firstData, secondData, specialists } from "./aboutData";

function About() {
  const [persons, setPerson] = useState([]);



  useEffect(() => {
    getPhotos(4, setPerson);
  }, []);

  return (
    <>
      <InfoPage {...InfoPageData} />
      <AboutUS {...firstData} />
      <AboutUS revers {...secondData} />
      <Departments />
      <Specialist {...specialists} persons={persons} />
      <Clients />
    </>
  );
}

export default About;
