import React, { useRef, useEffect } from "react";
import { Link, NavLink } from "react-router-dom";
import IconsLink from "../IconsLink";
import socialData from "./socialData";

import "./Navbar.scss";
import Logo from "../Logo";
import { useGlobalContext } from "../../context/context";

function Navbar() {
  const { openMenu, closeMenu, isMenuOpen } = useGlobalContext();

  const left = useRef(null);

  useEffect(() => {
    if (!isMenuOpen) {
      left.current.style.left = "-130%";
    }
    if (isMenuOpen) {
      left.current.style.left = "0%";
    }
  }, [isMenuOpen]);

  return (
    <div className="navbar-section">
      <nav className="navbar container">
        <div ref={left} className="navbar__menu menu">
          <ul onClick={closeMenu} className="menu__items">
            <li className="menu__item">
              <NavLink exact to="/about" className="menu__link">
                About
              </NavLink>
            </li>
            <li className="menu__item">
              <NavLink to="/doctor" className="menu__link">
                Doctor
              </NavLink>
            </li>
            <li className="menu__item">
              <NavLink to="/blog" className="menu__link">
                Blog
              </NavLink>
            </li>
            <li className="menu__item">
              <NavLink to="/contact" className="menu__link">
                Contact
              </NavLink>
            </li>
          </ul>
          {isMenuOpen && (
            <button onClick={closeMenu} className="menu__close">
              Close
            </button>
          )}
        </div>
        <div className="navbar__logo">
          <Link to="/">
            <Logo />
          </Link>
        </div>
        <div className="navbar__social">
          <IconsLink social={socialData} />
        </div>
        <button onClick={openMenu} className="burger">
          Menu
        </button>
      </nav>
    </div>
  );
}

export default Navbar;
