import React from "react";
import CardAddress from "../../components/CardAddress";
import "./CardsContacts.scss";

function CardsContacts({ contacCard }) {
  return (
    <div className="container">
      <div className="row">
        {contacCard.map(({id, ...contacCard}) => {
          return(
          <div key={id} className="col d-flex justify-content-center">
            <CardAddress {...contacCard} />
          </div>

          );
        })}
      </div>
    </div>
  );
}

export default CardsContacts;
