import React from "react";
import "./CardAbout.scss";


function CardAbout() {
    const cards = [
        {
          id: 1,
          title: "Drug and Alcohol Testing",
          descr: `Lorem Ipsum is simply dummy text of the printing and typesetting
          industry. Lorem Ipsum has been the industry's standard dummy
          text ever since the 1500s.`,
        },
        {
          id: 2,
          title: "Teeth Testing",
          descr: `Lorem Ipsum is simply dummy text of the printing and typesetting
          industry. Lorem Ipsum has been the industry's standard dummy
          text ever since the 1500s.`,
        },
        {
          id: 3,
          title: "Teeth Testing",
          descr: `Lorem Ipsum is simply dummy text of the printing and typesetting
          industry. Lorem Ipsum has been the industry's standard dummy
          text ever since the 1500s.`,
        },
        {
          id: 4,
          title: "Drug and Alcohol Testing",
          descr: `Lorem Ipsum is simply dummy text of the printing and typesetting
          industry. Lorem Ipsum has been the industry's standard dummy
          text ever since the 1500s.`,
        },
      ];
  return (
    <div className="department container">
      <div className="row">
        {cards.map((card) => {
          return (
            <div key={card.id} className="col-12 col-md-6 col-xl-3">
              <h5 className="department__title">{card.title}</h5>
              <p className="department__content">
               {card.descr}
              </p>
            </div>
          );
        })}
      </div>
    </div>
  );
}

export default CardAbout;
