import React from "react";
import InfoCard from "../InfoCard";
import "./CardBlog.scss";

function CardBlog({image}) {
  return (
    <div className="blog-card">
      <div className="blog-card__img">
        <div>
          <img
            src={image}
            alt="clients"
          />
        </div>
        <InfoCard />
      </div>
      <div className="blog-card__content">
        <h5 className="blog-card__title">
          Lorem Ipsum is simply dummy text of the printing and typesetting
          industry.
        </h5>
        <p className="blog-card__desc">
          Your bedroom is where you begin and end every day, and when it’s
          styled just how you like it, it can set the perfect tone. Whether
          you’re making a few easy updates or starting fresh ...
        </p>
      </div>
    </div>
  );
}

export default CardBlog;
