import React from "react";
import './HalfSection.scss'

function HalfSection({ img, children, revers, center }) {
  const reversSection = revers ? "flex-row-reverse" : "";
  const contentCenter = center ? " d-flex flex-column justify-content-center" : ""

  return (
    <div className="container half">
      <div className={`row d-flex ${reversSection}`}>
        <div className="col-12 col-lg-6">
          <img src={img} alt="dentel" />
        </div>
        <div className={`col-12 col-lg-6 ${contentCenter}`}>{children}</div>
      </div>
    </div>
  );
}

export default HalfSection;
