export const getApiResource = async (number) => {
  try {
    const res = await fetch(
      `https://api.unsplash.com/search/photos?&query=portrait-doctor&per_page=${number}`,
      {
        headers: {
          Authorization:
            "Client-ID S8cl9wfxmRb88xkUKnAT_QxzbP1Eh13mbFBk7EubtFk",
          "Accept-Version": "v1",
        },
      }
    );

    if (!res.ok) {
      console.error("Could not fetch.", res.status);
      return false;
    }

    return await res.json();
  } catch (error) {
    console.error("Could not fetch.", error.message);
    return false;
  }
};

export const getPhotos = async (number, setData) => {
    const res = await getApiResource(number);
    if (res) {
      setData(res.results);
    }
  };
