import React from "react";
import { Link } from "react-router-dom";
import Info from "../../components/Info";
import "./infoPage.scss";

export default function InfoPage({ bg, color, linkPage }) {
  return (
    <Info bg={bg} color={color}>
      <div className="title-page d-flex flex-column align-items-center">
        <h2 className="title-page__title">{linkPage}</h2>
        <div className="title-page__link">
          <Link to="/" >
            Home
          </Link>
          <span>/{linkPage}</span>
        </div>
      </div>
    </Info>
  );
}
