import React from "react";
import CardPersons from "@components/CardPersons";
import IconsLink from "@components/IconsLink";
import "./Specialist.scss";

const socialData = [
  { id: 1, name: "instagram" },
  { id: 2, name: "facebook" },
  { id: 3, name: "twitter" },
];



function Specialist({ dataSpecialist, persons }) {

  return (
    <div className="specialist">
      <h3 className="specialist__title">
        Our Distinguished Dental Specialists
      </h3>
      <CardPersons data={dataSpecialist} persons={persons}>
        <IconsLink social={socialData} />
      </CardPersons>
    </div>
  );
}

export default Specialist;
