import React from "react";
import Card from "../../components/Card";
import "./Cards.scss";



function Cards({cards}) {
  return (
    <div className="cards container">
      <div className="row">
        {cards.map((card) => {
          return (
            <div key={card.id} className="col-12 col-md-6 col-xl-3">
              <Card  card={card} />
            </div>
          );
        })}
      </div>
    </div>
  );
}

export default Cards;
