import React, { useEffect, useState } from "react";
import Cards from "../Layout/Cards";
import Hero from "../Layout/Hero";
import InfoHome from "../Layout/InfoHome";
import TextContent from "../Layout/TextContent";
import Service from "../Layout/Service";
import Specialist from "../Layout/Specialist";
import Clients from "../Layout/Clients";
import Blog from "../Layout/Blog";

import { getPhotos } from "../services/getService";

import {
  heroImage,
  heroData,
  blueCards,
  TextContet,
  specialists,
  blogImg,
} from "./homeData";

function Home() {
  const [persons, setPerson] = useState([]);



  useEffect(() => {
    getPhotos(8, setPerson);
  }, []);

  return (
    <>
      <Hero {...heroImage} />
      <InfoHome {...heroData} />
      <Cards {...blueCards} />
      <TextContent {...TextContet} />
      <Service />
      <Specialist {...specialists}  persons={persons}/>
      <Clients />
      <Blog {...blogImg} />
    </>
  );
}

export default Home;
