import React from "react";
import CardLink from "../../components/CardLink";
import "./Service.scss";

function Service() {
  return (
    <div className="service">
      <h3 className="service__title">Our Umbrella of Services</h3>
      <h4 className="service__subtitle">Pain free procedures in dentistry</h4>
      <div className="container">
        <CardLink />
      </div>

      <a className="service__link" href="/">
        Explore all <i className="arrow arrow-link"></i>
      </a>
    </div>
  );
}

export default Service;
