import React from "react";
import "./Form.scss";

function Form() {
  return (
    <div className="form py-5 container">
      <h3 className="form__title">Get in touch</h3>
      <div className="form__user user row">
        <div className="user__name col-12 col-md-6">
          <label htmlFor="name">Your Name</label>
          <input type="text" name="name" />
        </div>
        <div className="user__email col-12 col-md-6">
          <label htmlFor="subject">Your Email</label>
          <input type="Email" name="email" />
        </div>
      </div>
      <label htmlFor="subject">Subject</label>
      <input className="form__subject" type="text" />
      <label htmlFor="message">Your Message</label>
      <textarea
        className="form__message"
        name="message"
        cols="30"
        rows="10"
      ></textarea>
      <button className="btn form__submit">Submit</button>
    </div>
  );
}

export default Form;
