import React from "react";
import "./IconsLink.scss";

function IconsLink({ social }) {
  return (
    <>
      {social.map(({id, name}) => {
        return (
          <a key={id} href="/#" className="social">
            <i className={`social__icon + ${name} `}></i>
          </a>
        );
      })}
    </>
  );
}

export default IconsLink;
