import React from "react";
import "./TextContent.scss";
import HalfSection from "../../components/HalfSection";



function TextContent({img, dataText}) {
  return (
    <section className="content">
      <HalfSection img={img}>
        <h3 className="content__title">Relax your dentist knows best</h3>
        {dataText.map((data) => {
          return (
            <div key={data.id}>
              <h4 className="second-title">{data.title}</h4>
              <p className="content__description">{data.description}</p>
            </div>
          );
        })}
      </HalfSection>
    </section>
  );
}

export default TextContent;
